.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login__signInButton{
    width: 100%;
    height: 30px;
    background: #f0c14b;
    border:1px solid;
    margin-top: 10px;
    margin-bottom: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color :#111;
}
.login__registerButton
{
    width: 100%;
    height: 30px;
    border-radius: 2px;
    border:1px solid;
    margin-top: 10px;
    border-color:darkgrey;
}
.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border:1px solid lightgray;
}
.login__container>h1{
    font: weight 500 ;
    margin-bottom: 20px;
}
.login__container>p{
    margin-top: 15px;
    font-size: 12px;
}
.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100px;
    object-fit: contain;
}
.login__container>form>h5{
    margin-bottom: 5px;
}
.login__container>form>input{
    margin-bottom: 10px;
    height: 30px;
    background-color: white;
    width: 98%;
}